.program-selection-form {
  padding-bottom: 32px;
}

.program-selection-form__main-title {
  margin-top: 27px;
  margin-bottom: 37px;
}

.program-selection-form__description {
  margin: 0;
  margin-bottom: 39px;
  text-transform: uppercase;
}

.program-selection-form__container {
  margin: 0;
  padding: 0;
  border: none;
}

.program-selection-form__container--bordered {
  margin-top: 24px;
  padding-top: 22px;
  padding-bottom: 19px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    height: 1px;
    left: -20px;
    right: -20px;
    top: -1px;
    background-color: #e7e7e7;
  }

  &::after {
    content: "";
    position: absolute;
    height: 1px;
    left: -20px;
    right: -20px;
    bottom: 0;
    background-color: #e7e7e7;
  }
}

.program-selection-form__lined-title {
  padding-left: 0;
  width: fit-content;
}

.program-selection-form__textarea {
  padding: 14px;
  padding-left: 15px;
  width: 100%;
  min-height: 150px;
  resize: none;
  border: 1px solid #e7e7e7;
  font-family: inherit;
  font-size: 16px;
  line-height: inherit;
  text-transform: uppercase;
  color: #444444;
}

.program-selection-form__button {
  margin-top: 34px;
  margin-bottom: 21px;
  font-family: inherit;
  font-size: 16px;
}

.program-selection-form__tip {
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 16px;
}
